import React, { Component } from "react";
import { Switch, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import 'react-app-polyfill/stable'
import AuthService from "./services/auth.service";

import Login from "./components/LoginComponent";
import Register from "./components/RegisterComponent";
import Home from "./components/HomeComponent";
import Profile from "./components/ProfileComponent";
import BoardUser from "./components/BoardUserComponent";
import BoardModerator from "./components/board-moderator.component";
import BoardAdmin from "./components/BoardAdminComponent";


import ListTaskComponent from './components/ListTaskComponent';
import ViewTaskComponent from './components/ViewTaskComponent';
import AddTaskComponent from './components/AddTaskComponent';
import EditTaskComponent from './components/EditTaskComponent';

class App extends Component {
  constructor(props) {
    super(props);
    this.logOut = this.logOut.bind(this);

    this.state = {
      
      showAdminBoard: false,
      currentUser: undefined,
    };
  }

  componentDidMount() {
    const user = AuthService.getCurrentUser();

    if (user) {
      this.setState({
        currentUser: user,
        
        showAdminBoard: user.roles.includes("ROLE_ADMIN"),
      });
    }
  }

  logOut() {
    AuthService.logout();
  }

  render() {
    const { currentUser,  showAdminBoard } = this.state;

    return (
      <div>
        <div><Link to={"/"} > Home</Link></div>
        <div>
        {showAdminBoard && (
              <li className="nav-item">
                <p> You are admin</p>
              </li>
            )}
        {currentUser && (
             
               <Link to={"/tasks"} >
                 Tasks
               </Link>
             
              
            )}

          </div>
        

            <div>
            {currentUser ? (
              <div>
              <a href="/login"  onClick={this.logOut}>
              LogOut
            </a>
            </div>
            
          ) : (
            <div>
               <Link to={"/login"}>
                  Login
                </Link>
             
                <Link to={"/register"} >
                  Sign Up
                </Link>
             </div>     
               
           
          )}



            </div>


         

          
        

        <div className="container mt-3">
          <Switch>
            <Route exact path={["/", "/home"]} component={Home} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/register" component={Register} />
            <Route exact path="/profile" component={Profile} />
            
            <Route path = "/tasks" component = {ListTaskComponent}></Route>
            <Route path = "/tasks/:id" component = {EditTaskComponent}></Route>
            <Route path = "/add-task/:id" component = {AddTaskComponent}></Route>
            <Route path = "/view-task/:id" component = {ViewTaskComponent}></Route>
          </Switch>
        </div>
      </div>
    );
  }
}

export default App;
