import axios from 'axios'
import authHeader from './auth-header';

//const TASKS_REST_API_URL = 'http://localhost:8080/api/tasks';
const TASKS_REST_API_URL = 'https://jwt-user-tasks.herokuapp.com/api/tasks'
class TaskService {
    getTasks() {
        return axios.get(TASKS_REST_API_URL, { headers: authHeader() });
    }

    getTaskById(taskId) {
        return axios.get(TASKS_REST_API_URL + '/' + taskId, { headers: authHeader() });
    }

    addTask(task) {
        return axios.post(TASKS_REST_API_URL +'/', task, { headers: authHeader() });
    }

    editTask(task, taskId) {
        return axios.put(TASKS_REST_API_URL + "/" + taskId, task, { headers: authHeader() });
    }


    deleteTask(taskId) {
        return axios.delete(TASKS_REST_API_URL + "/" + taskId, { headers: authHeader() });
    }

}

export default new TaskService()